@charset "UTF-8";
@font-face {
  font-family: 'TTLakes';
  font-style: normal;
  font-weight: medium;
  src: local("TTLakes"), url(./fonts/TTLakes-Medium.woff) format("woff"); }

@font-face {
  font-family: 'TTLakes';
  font-style: normal;
  font-weight: normal;
  src: local("TTLakes"), url(./fonts/TTLakes-Regular.woff) format("woff"); }

/* Жирный */
@font-face {
  font-family: 'TTLakes';
  font-style: normal;
  font-weight: bold;
  src: local("TTLakes"), url(./fonts/TTLakes-Bold.woff) format("woff"); }

body {
  font-family: 'TTLakes', sans-serif; }

.block1 {
  border: 0px solid #097e5f;
  width: 94%;
  margin-left: 24px;
  margin-right: 24px; }

ul,
li {
  margin: 0;
  padding: 0;
  text-decoration: none; }

.line,
.main-line,
.line__item {
  display: flex;
  width: auto; }

/*heder*/
.line {
  justify-content: space-between;
  align-items: center;
  background-color: white;
  padding: 0 7px; }

.line__item {
  justify-content: space-around;
  align-items: center; }

.line__group img {
  margin-top: 5px; }

.line__der {
  border: 1px solid #e3e3ec;
  border-radius: 50%;
  height: 26px; }

.line__item div {
  margin-left: 10px; }

/*heder*/
/*main */
.tittle {
  display: flex;
  justify-content: space-between; }

h1 {
  font-family: 'TTLakes', sans-serif;
  font-weight: bold; }

h1::before {
  content: ".";
  margin-left: -24px;
  margin-right: 10px;
  color: #B8FFEC;
  background-color: #B8FFEC; }

main {
  background-color: #F8F8F8; }

.main-line a {
  display: flex;
  font-size: 18px;
  padding: 5px 10px;
  margin: 5px;
  text-decoration: none;
  color: black;
  height: 24px;
  align-self: center; }

.main-line img {
  margin-left: 10px;
  align-self: center; }

.nav-list {
  display: flex; }

.nav-list__item {
  box-sizing: border-box; }

.nav-list__item a {
  font-size: 18px;
  padding: 3px 7px;
  margin: 0 0px;
  text-decoration: none;
  color: #7E7E82;
  height: 24px;
  text-align: center;
  border-radius: 5px;
  border: 2px solid #F8F8F8; }

.nav-list__item:hover {
  background-color: white;
  border-color: #B8FFEC; }

.nav-list li:nth-child(1) a {
  width: 210px; }

.nav-list li:nth-child(2) a {
  width: 250px; }

.nav-list li:nth-child(3) a {
  width: 210px; }

.nav-list li:nth-child(4) a {
  width: 140px; }

.nav-list li:nth-child(5) a {
  width: 230px; }

.nav-list li:nth-child(6) a {
  width: 180px; }

.block1 h2 {
  font-family: 'TTLakes', sans-serif;
  font-weight: normal;
  margin-left: 10px;
  color: #7E7E82; }

section.text {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-top: 0px; }

.text-text__articl {
  display: block;
  float: left;
  font-size: 17px;
  height: auto;
  margin-left: 5px;
  margin-top: 5px;
  margin-right: 20px;
  color: #7E7E82;
  overflow: hidden; }

.text-text__link a {
  text-decoration: none;
  color: black;
  font-size: 18px; }

.text-text__link img {
  vertical-align: middle; }

.text-img img {
  display: block;
  width: 100%;
  height: 100%; }

@media screen and (min-width: 1121px) {
  .nav-list__item:nth-of-type(3),
  .line {
    display: none; } }

@media screen and (min-width: 768px) and (max-width: 1120px) {
  .main-line {
    display: none; }
  .nav-list li:first-child,
  .nav-list li:nth-child(5) {
    display: none; }
  .text-text__articl {
    height: 180px; } }

@media screen and (max-width: 768px) {
  /*heder*/
  .scrin__360 {
    display: none; }
  section.text {
    grid-template-columns: 1fr; }
  .main-line {
    display: none; }
  .nav-list li:first-child,
  .nav-list li:nth-child(5) {
    display: none; }
  section .back_menu {
    display: block; } }
