body {
  font-family: 'TTLakes', sans-serif;
  border: 0px dotted #6200ff; }

.block3,
.footer {
  width: 94%;
  background-color: #f8f8f8;
  margin-left: 24px;
  margin-right: 24px; }

table {
  border-collapse: collapse;
  height: 100%;
  width: 100%; }

tr, td, th, .rect td {
  padding: 0;
  margin: 0;
  font-size: 14px; }

.top-left {
  border-top-left-radius: 20px; }

.top-right {
  border-top-right-radius: 20px; }

.bot-left {
  border-bottom-left-radius: 20px; }

.bot-right {
  border-bottom-right-radius: 20px; }

th {
  height: 16px;
  text-align: start;
  color: #7e7e82;
  padding-left: 16px;
  padding-top: 31px;
  padding-bottom: 16px; }

td {
  height: 18px;
  padding-left: 16px;
  padding-top: 16px;
  padding-bottom: 16px;
  background-color: white; }

.call {
  padding: 0;
  margin: 0;
  display: flex;
  height: 32px;
  width: 119px;
  margin: 0px 0px;
  justify-content: space-between;
  background-color: #FF3E79;
  border-radius: 20px;
  margin-right: 16px; }

.call__goside,
.call__text {
  color: white;
  font-size: 12px;
  font-weight: bold;
  margin-top: auto;
  margin-bottom: auto;
  margin-right: 4px;
  margin-left: 12px; }

.rect {
  padding: 0;
  margin: 0;
  height: 0px;
  width: 100%;
  margin-left: 16px;
  padding-left: 16px;
  border-radius: 20px; }

.rect td {
  height: 0px;
  padding-right: 16px;
  padding-left: 16px; }

.rect-free {
  height: 1px;
  border: 1px solid #EAEAEA; }

.tabl {
  display: block;
  height: 216px;
  width: 260px;
  background-color: white;
  text-align: start;
  border-radius: 20px; }

.tabl-uslugi,
.tabl-cena,
.tabl-srok {
  color: #7e7e82;
  margin-top: 16px; }

.tabl div {
  padding-left: 16px; }

.tabl-zakaz {
  display: flex;
  justify-content: space-between;
  padding-bottom: 16px; }

.swip-pad {
  height: 32px; }

.block3-text > p {
  color: #1B1C21;
  font-size: 14px;
  line-height: 20px; }

.block3-text {
  height: 60px;
  width: 421px; }

.block3-src {
  margin-top: 16px;
  display: flex;
  height: 24px;
  width: 326px;
  justify-content: space-between;
  text-align: center; }

.block3-src > div {
  font-size: 16px;
  line-height: 24px;
  color: #1B1C21;
  font-weight: 500; }

.footer {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  margin-top: 47px;
  align-items: flex-start; }

.footer__item {
  width: 220px;
  margin-top: 32px;
  line-height: 16px;
  font-size: 14px;
  color: #7E7E82; }

@media screen and (max-width: 768px) {
  .slaid-cats,
  .slaid-remonts,
  .slaid-tab {
    display: block;
    border: 2px solid #200ae2; }
  table {
    display: none; }
  .block3 {
    width: 320px; }
  .footer {
    flex-direction: column;
    text-align: center; }
  .footer__item {
    margin-top: 16px;
    margin-left: auto;
    margin-right: auto; } }
